@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:'Source Sans Pro', sans-serif}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.app-wrapper{position:relative;height:100vh}.app-content{height:97vh;display:flex}

.bottom-nav-wrapper{display:flex;justify-content:center;align-items:center;height:3vh;background:#52616b;color:#eee}

.canvas-wrapper{width:50vw;height:100%;position:relative}.canvas-main{background:#f9f7f7;display:flex;flex-direction:column;justify-content:center;align-items:center;width:100%;height:90%;cursor:pointer}.canvas-hint{position:absolute;top:5px;color:#959ba585;pointer-events:none;text-align:center}.canvas-toolbox{width:100%;height:10%;background:#dbe2ef;display:flex;justify-content:center;align-items:center}

.result-wrapper{width:50vw;height:100%}.result-main{position:relative;background:#3f72af;display:flex;justify-content:center;align-items:center;width:50vw;height:90%}.result-toolbox{width:100%;height:10%;background:#2b63a8;display:flex;justify-content:center;align-items:center}

